<template>
    <el-card class="box-card">
        <el-breadcrumb separator-class="el-icon-arrow-right" class='breadcrumb'>
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
            <el-breadcrumb-item>用户列表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-row :gutter="30">
            <el-col :span="8">
                <el-input placeholder="请输入后搜索" v-model="serachKey" autofocus=true clearable>
                    <el-button slot="append" icon="el-icon-search" @click='handleSearch(serachKey)'></el-button>
                </el-input>
            </el-col>
            <el-col :span='4'>
                <el-button type='primary' @click='handleAddBooks'>添加用户</el-button>
            </el-col>
        </el-row>

        <el-table
            :data="tableData.slice((tableRelevant.pageNow-1)*(tableRelevant.pageSize),(tableRelevant.pageNow)*(tableRelevant.pageSize))"
            style="width: 100%" border row-key :header-cell-style="{color:'#606266'}" class='booktable'>
            <el-table-column prop="username" label="姓名" align="center">
            </el-table-column>
            <el-table-column prop="rolename" label="权限" align="center">
            </el-table-column>
            <el-table-column prop="email" label="邮箱" align="center">
            </el-table-column>
            <el-table-column prop="userrole" label="角色" align="center">
            </el-table-column>
            <el-table-column prop='now' label='状态' align="center">
                <template slot-scope='scope'>
                    <el-switch v-model="scope.row.now" active-color="#13ce66" inactive-color="#ff4949">
                    </el-switch>
                </template>
            </el-table-column>
            <el-table-column prop='something' label='操作' align='center'>
                <template slot-scope='scope'>
                    <el-button type="primary" icon="el-icon-edit" circle @click='handleTrue(scope.$index,scope.row)'>
                    </el-button>
                    <el-button type="danger" icon="el-icon-delete" circle @click='handleDelete(scope.$index,scope.row)'>
                    </el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="添加用户" :visible="addBookVisible" width="30%" :before-close="handleAddBookFalse"
            @close='EditBookData'>
            <el-form :model="addUsers" :rules="addUsersRules" label-width="70px" class="demo-ruleForm" ref='addUsers'>
                <el-form-item label="用户名" prop="addBookname">
                    <el-input v-model="addUsers.addBookname"></el-input>
                </el-form-item>
                <el-form-item label="权限" prop="addBooktitle">
                    <el-input v-model="addUsers.addBooktitle"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="addBookprice">
                    <el-input v-model="addUsers.addBookprice"></el-input>
                </el-form-item>
                <el-form-item label="角色" prop="addBooknum">
                    <el-input v-model="addUsers.addBooknum"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addBookVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleAddBookTrue">确 定</el-button>
            </span>
        </el-dialog>

        <el-dialog title="修改图书信息" :visible="dialogVisible" width="30%" :before-close="handleFalse">
            <el-form :model="editBook" :rules="editBookRules" label-width="70px" class="demo-ruleForm" ref='editBook'>
                <el-form-item label="姓名" prop="bookname">
                    <el-input v-model="editBook.bookname"></el-input>
                </el-form-item>
                <el-form-item label="权限" prop="remanname">
                    <el-input v-model="editBook.remanname"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="bookprice">
                    <el-input v-model="editBook.bookprice"></el-input>
                </el-form-item>
                <el-form-item label="角色" prop="booknum">
                    <el-input v-model="editBook.booknum"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleEditTrue">确 定</el-button>
            </span>
        </el-dialog>

        <el-pagination class='pagination' @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="tableRelevant.pageNow" background :page-sizes="[1, 3, 5, 10]"
            :page-size="tableRelevant.pageSize" layout="total, sizes, prev, pager, next, jumper"
            :total="tableData.length">
        </el-pagination>
    </el-card>
</template>

<script>
    import axios from 'axios'
    export default {
        data() {
            return {
                serachKey: '',
                tableRelevant: {
                    pageNow: 1,
                    pageSize: 3,
                    index: ''
                },
                tableData: [],
                addBookVisible: false,
                dialogVisible: false,
                addUsers: {
                    addBookname: '', addBooktitle: '', addBookprice: '', addBooknum: ''
                },
                addUsersRules: {
                    addBookname: [
                        { required: true, message: '请输入书籍名称', trigger: 'blur' }
                    ], addBooktitle: [
                        { required: true, message: '请输入书籍作者', trigger: 'blur' }
                    ], addBookprice: [
                        { required: true, message: '请输入书籍价格', trigger: 'blur' }
                    ], addBooknum: [
                        { required: true, message: '请输入书籍库存', trigger: 'blur' }
                    ]
                },
                editBook: {
                    bookname: '', remanname: '', bookprice: '', booknum: ''
                },
                editBookRules: {
                    bookname: [
                        { required: true, message: '请输入书籍名称', trigger: 'blur' }
                    ], remanname: [
                        { required: true, message: '请输入作者名称', trigger: 'blur' }
                    ], bookprice: [
                        { required: true, message: '请输入书籍价格', trigger: 'blur' }
                    ], booknum: [
                        { required: true, message: '请输入书籍库存', trigger: 'blur' }
                    ]
                }
            }
        }, watch: {
            serachKey() {
                if (this.serachKey == '') {
                    this.getUsersInFo()
                }
            }
        }, created() {
            this.getUsersInFo()
        }, methods: {
            getUsersInFo() {
                axios.get('../mock/mock.json').then(res => {
                    const data = res.data.data.users
                    this.tableData = data
                })
            },
            handleSizeChange(newSize) {
                this.tableRelevant.pageSize = newSize
            }, handleCurrentChange(newNow) {
                this.tableRelevant.pageNow = newNow
            }, handleSearch(serachKey) {
                console.log(1)
                const mineDatas = []
                this.tableData.forEach((value, index) => {
                    if (value.username.indexOf(serachKey) > -1 || value.rolename.indexOf(serachKey) > -1 || value.userrole.indexOf(serachKey) > -1 || value.email.indexOf(serachKey) > -1) {
                        mineDatas.push(value)
                    }
                })
                this.tableData = mineDatas
            }, handleAddBooks() {
                this.addBookVisible = true
            }, handleAddBookFalse() {
                this.addBookVisible = false
            }, EditBookData() {
                this.$refs.addUsers.resetFields()
            }, handleAddBookTrue() {
                this.addBookVisible = false
                this.$refs.addUsers.validate(valid => {
                    if (!valid) return
                    this.tableData.unshift({ 'username': this.addUsers.addBooktitle, 'rolename': this.addUsers.addBookname, 'userrole': this.addUsers.addBookprice, 'email': this.addUsers.addBooknum })
                    this.$message.success('用户添加成功!')
                })
            }, handleTrue(index, res) {
                this.dialogVisible = true
                this.tableRelevant.index = index

            }, handleFalse() {
                this.dialogVisible = false
            }, handleEditTrue() {
                if (this.editBook.bookname == '' || this.editBook.remanname == '' || this.editBook.bookprice == '' || this.editBook.booknum == '') {
                    this.$message({
                        message: '用户信息未填写完整!'
                    })
                } else {
                    this.tableData[this.tableRelevant.index].username = this.editBook.bookname
                    this.tableData[this.tableRelevant.index].rolename = this.editBook.remanname
                    this.tableData[this.tableRelevant.index].userrole = this.editBook.bookprice
                    this.tableData[this.tableRelevant.index].email = this.editBook.booknum
                    this.$message.success('用户信息修改成功!')
                }
                this.dialogVisible = false
                this.$refs.editBook.resetFields()
            }, async handleDelete(index, res) {
                const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).catch(err => err)
                if (confirmResult !== 'confirm') {
                    return this.$message.info('已经取消删除操作')
                }
                this.$message.success('成功删除该用户')
                this.tableData.splice(index, 1)
            }
        }
    }
</script>

<style scoped lang='stylus'>

</style>