<template>
    <div class='login' :style='loginStyle'>
        <div class="login_body animated fadeInLeft">
            <!-- 顶部图片 -->
            <div class="login_img">
                <img :src="loginImg" alt="" @mouseenter="enter" @mouseleave="leave" ref='img'>
            </div>
            <!-- 登陆表单区域 -->
            <form class='form'>
                <div v-show='!show'>
                    <el-input placeholder="用户名" v-model="username" clearable> <i class='iconfont icon icon-people'
                            slot='prefix'></i>
                    </el-input>
                    <el-input placeholder="请输入密码" v-model="password" show-password> <i class='iconfont icon icon-lock'
                            slot='prefix'></i></el-input>
                    <div class='form-suc'>
                        <button class='form-btn' @click='succLogin' type='button'>登录</button>
                        <el-button type="info" @click='again'>重置</el-button>
                    </div>
                    <div class='form-fal'>
                        <a href="#" @click="show = !show">无身份?请注册</a>
                    </div>
                </div>
                <div class='form-zc' v-show='show'>
                        <div v-show="show" class='zcdiv'>
                            <el-row  class="row-bg" >
                                <el-col :span="6"><div class="grid-content bg-purple">身份:</div></el-col>
                                <el-col :span="18"><el-input
                                    placeholder="请输入身份"
                                    v-model="zcpeo"
                                    clearable>
                                  </el-input>
                                  </el-col>
                              </el-row>
                              <el-row  class="row-bg" >
                                <el-col :span="6"><div class="grid-content bg-purple">用户名:</div></el-col>
                                <el-col :span="18"><el-input
                                    placeholder="请输入用户名"
                                    v-model="zcuser"
                                    clearable>
                                  </el-input>
                                  </el-col>
                              </el-row>
                              <el-row  class="row-bg" >
                                <el-col :span="6"><div class="grid-content bg-purple">密码:</div></el-col>
                                <el-col :span="18"><el-input
                                    placeholder="请输入密码"
                                    v-model="zcpass"
                                    clearable>
                                  </el-input>
                                  </el-col>
                              </el-row>
                              <el-row  class="row-bg" >
                                <button type='button' @click="show = !show">登陆</button>
                                    <button type='button' @click='zcusers'>注册</button>
                              </el-row>
                        </div>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: 'login',
        data() {
            return {
                loginStyle: {},
                loginImg: require('.././assets/bgImg/img2.jpg'),
                username: '',
                password:'',
                show: false,
                zcpeo:'',
                zcuser:'',
                zcpass:''
            }
        },
        created() {

            this.loginStyle.backgroundImage = 'url(' + require('.././assets/bgImg/img' + new Date().getDay() + '.jpg') + ')'
        },
        methods: {
            enter: function () {
                this.$refs.img.style.transform = 'rotate(360deg) scale(3)'
            }, leave: function () {
                this.$refs.img.style.transform = 'rotate(0deg) scale(1)'
            },again(){
                this.username=''
                this.password=''
            },succLogin(){


            axios.get('http://localhost:8080/yongHu/denglu',{
              params:{
                name:this.username,
                password:this.password
              }
            })
              .then(function (response) {
                console.log(response);
              })
              .catch(function (error) {
                console.log(error);
              });
            this.$router.push('/home')
            },zcusers(){
                setTimeout(() => {
                    this.$message({
                        message: '注册成功',
                        type: 'success'
                    });
                }, 500);
                axios.get('http://localhost:8080/yongHu/denglu',{
                    params:{
                        username:this.username,
                        password:this.password
                    }
                })
                    .then(function (response) {
                        console.log(response);
                    })
                    .catch(function (error) {
                        console.log(error);
                    });
            },

        }
    }
</script>

<styls scoped lang='stylus'>
    .login
        height 100vh
        background-repeat no-repeat
        background-size 100% 100%
        display flex
        justify-content center
        align-items center
        .login_body
            width 400px
            height 300px
            background-color #fff
            display flex
            flex-direction column
            align-items center
            justify-content center
            position relative
            border-radius 10px
            form
                  padding-top 70px
                .form-zc
                    width 100%
                    display flex
                    flex-direction column
                    align-self flex-end
                    margin-top -50px
                    .zcdiv
                        padding 4px
                        width 230px
                        display flex
                        flex-direction column
                        .el-row
                            display flex
                            flex-direction row
                            font-size 14px
                            button
                                cursor pointer
                                margin-right 15px
                                padding 0 20px
                                border-radius 4px
                                box-shadow none
                                border none
                                height 39.32px
                                color white
                                background-color #67C23A
                div
                    display flex
                    flex-direction column
                    justify-content center
                    align-items center
                    .el-input
                        width 100%
                        margin 5px
                        span
                            display flex
                            flex-direction column
                            justify-content center
                            .icon
                                font-size 150%
                    .form-suc
                            margin-top 10px
                            display flex
                            flex-direction row
                            align-self flex-end
                        .form-btn
                            cursor pointer
                            margin-right 15px
                            padding 0 20px
                            border-radius 4px
                            box-shadow none
                            border none
                            height 39.32px
                            color white
                            background-color #67C23A
                    .form-fal
                        display flex
                        flex-direction row
                        align-self flex-end
                        font-size 12px
                        margin-top 20px
                        a
                            color #409EFF
            .login_img
                width 100px
                height 100px
                border thin solid #eee
                border-radius 50%
                overflow hidden
                position absolute
                top -40px
                background-color #eee
                box-shadow 1px 1px 30px #333
                display flex
                justify-content center
                align-items center
                img
                    width 98px
                    height 98px
                    border-radius 50%
                    transition all ease 1s
            .login_form
                position absolute
                bottom 20px
                width 80%
                .form_btn
                    display flex
                    justify-content flex-end
    @media screen and (max-width: 600px)
        .login
            .login_body
                width 75%
                height 50%
</styls>
