<template>
    <el-card class="box-card">
        <!-- 面包屑导航 -->
        <el-breadcrumb separator-class="el-icon-arrow-right" class='breadcrumb'>
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>图书管理</el-breadcrumb-item>
            <el-breadcrumb-item>图书列表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-row :gutter="30">
            <el-col :span="8">
                <!-- 输入搜索功能 -->
                <el-input placeholder="请输入后搜索" v-model="serachKey" autofocus=true clearable>
                    <el-button slot="append" icon="el-icon-search" @click='handleSearch(serachKey)'></el-button>
                </el-input>
            </el-col>
            <el-col :span='4'>
                <!-- 添加书籍功能 -->
                <el-button type='primary' @click='handleAddBooks'>添加书籍</el-button>
            </el-col>
        </el-row>

        <!-- 表格 -->
        <el-table
            :data="tableData.slice((tableRelevant.pageNow-1)*(tableRelevant.pageSize),(tableRelevant.pageNow)*(tableRelevant.pageSize))"
            style="width: 100%" border row-key :header-cell-style="{color:'#606266'}" class='booktable'>
            <!-- 行/列 -->
            <el-table-column prop="title" label="书名" align="center"  type="selection">
            </el-table-column>
            <el-table-column prop="name" label="作者" align="center">
            </el-table-column>
            <el-table-column prop="price" label="价格" align="center">
            </el-table-column>
            <el-table-column prop='number' label='库存' align="center">
            </el-table-column>
            <el-table-column prop='something' label='操作' align='center' show-overflow-tooltip>
                <!-- 修改/删除功能 -->
                <template slot-scope='scope'>       
                    <el-button type="primary" icon="el-icon-edit" circle @click='handleTrue(scope.$index,scope.row)'>
                    </el-button>
                    <el-button type="danger" icon="el-icon-delete" circle @click='handleDelete(scope.$index,scope.row)'>
                    </el-button>
                </template>
            </el-table-column>
        </el-table>

        <!-- 添加书籍对话框 -->
        <el-dialog title="添加书籍" :visible="addBookVisible" width="30%" :before-close="handleAddBookFalse"
            @close='EditBookData'>
            <el-form :model="addUsers" :rules="addUsersRules" label-width="70px" class="demo-ruleForm" ref='addUsers'>
                <el-form-item label="书名" prop="addBookname">
                    <el-input v-model="addUsers.addBookname"></el-input>
                </el-form-item>
                <el-form-item label="作者" prop="addBooktitle">
                    <el-input v-model="addUsers.addBooktitle"></el-input>
                </el-form-item>
                <el-form-item label="价格" prop="addBookprice">
                    <el-input v-model="addUsers.addBookprice"></el-input>
                </el-form-item>
                <el-form-item label="库存" prop="addBooknum">
                    <el-input v-model="addUsers.addBooknum"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addBookVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleAddBookTrue">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 修改图书信息对话框 -->
        <el-dialog title="修改图书信息" :visible="dialogVisible" width="30%" :before-close="handleFalse">
            <el-form :model="editBook" :rules="editBookRules" label-width="70px" class="demo-ruleForm" ref='editBook'>
                <el-form-item label="书名" prop="bookname">
                    <el-input v-model="editBook.bookname"></el-input>
                </el-form-item>
                <el-form-item label="作者" prop="remanname">
                    <el-input v-model="editBook.remanname"></el-input>
                </el-form-item>
                <el-form-item label="价格" prop="bookprice">
                    <el-input v-model="editBook.bookprice"></el-input>
                </el-form-item>
                <el-form-item label="库存" prop="booknum">
                    <el-input v-model="editBook.booknum"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleEditTrue">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 分页 -->
        <el-pagination class='pagination' @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="tableRelevant.pageNow" background :page-sizes="[1, 3, 5, 10]"
            :page-size="tableRelevant.pageSize" layout="total, sizes, prev, pager, next, jumper"
            :total="tableData.length">
    </el-pagination>
    </el-card>
</template>

<script>
    import axios from 'axios'
    export default {
        data() {
            return {
                serachKey: '',      //搜索关键字
                tableRelevant: {
                    pageNow: 1,         //当前页码
                    pageSize: 3,         //每页数据条数
                    index: ''            //表格行索引
                },
                tableData: [],             //表格数据
                addBookVisible: false,   //添加书籍的对话框开关
                dialogVisible: false,   //图书信息修改的对话框开关
                addUsers: {             //添加用户的表单的数据绑定
                    addBookname: '', addBooktitle: '', addBookprice: '', addBooknum: ''     //表单验证对象
                },
                addUsersRules: {         //添加用户的表单验证规则对象
                    addBookname: [
                        { required: true, message: '请输入书籍名称', trigger: 'blur' }
                    ], addBooktitle: [
                        { required: true, message: '请输入书籍作者', trigger: 'blur' }
                    ], addBookprice: [
                        { required: true, message: '请输入书籍价格', trigger: 'blur' }
                    ], addBooknum: [
                        { required: true, message: '请输入书籍库存', trigger: 'blur' }
                    ]
                },
                editBook: {             //添加用户的表单的数据绑定
                    bookname: '', remanname: '', bookprice: '', booknum: ''      //表单验证对象
                },
                editBookRules: {         //添加用户的表单验证规则对象
                    bookname: [
                        { required: true, message: '请输入书籍名称', trigger: 'blur' }
                    ], remanname: [
                        { required: true, message: '请输入作者名称', trigger: 'blur' }
                    ], bookprice: [
                        { required: true, message: '请输入书籍价格', trigger: 'blur' }
                    ], booknum: [
                        { required: true, message: '请输入书籍库存', trigger: 'blur' }
                    ]
                }
            }
        }, watch: {
            serachKey() {   //监听搜索框内容是否为空
                if (this.serachKey == '') {
                    this.getUsersInFo()
                }
            }
        }, created() {
            this.getUsersInFo()     //开始渲染数据
        }, methods: {

            getUsersInFo() {    //获取数据
                axios.get('../mock/mock.json').then(res => {
                    const data = res.data.data
                    const setItemTableData = JSON.stringify(data.books)     //将数据转换为浏览可以识别的数据类型
                    window.sessionStorage.setItem('setItemTableData', setItemTableData)  //暂存数据
                    const getItemTableData = JSON.parse(window.sessionStorage.getItem('setItemTableData'))  //获取并转换数据类型暂存在浏览器中的数据
                    this.tableData = getItemTableData   //将最初加载的数据保存在浏览器中
                })
            },
            handleSizeChange(newSize) {      //监听pageSize(每页数据条数)改变的事件
                this.tableRelevant.pageSize = newSize
            }, handleCurrentChange(newNow) {    //监听pageNow(当前页码)改变的事件
                this.tableRelevant.pageNow = newNow
            }, handleSearch(serachKey) {        //根据搜索关键字筛选符合出要求的数据内容
                const mineDatas = []
                this.tableData.forEach((value, index) => {
                    if (value.title.indexOf(serachKey) > -1 || value.name.indexOf(serachKey) > -1 || value.price.indexOf(serachKey) > -1) {
                        mineDatas.push(value)
                    }
                })
                this.tableData = mineDatas  //重新赋值
            }, handleAddBooks() {         //书籍添加的对话框开启事件
                this.addBookVisible = true
            }, handleAddBookFalse() {     //书籍添加的对话框关闭事件
                this.addBookVisible = false
            }, EditBookData() {
                this.$refs.addUsers.resetFields()      //关闭对话框的同时清除表单中已填写过的数据
            }, handleAddBookTrue() {      //书籍添加的对话框确认事件
                this.addBookVisible = false
                this.$refs.addUsers.validate(valid => {    //校验表单是否填写完整
                    if (!valid) return
                    const addBook = { 'title': this.addUsers.addBooktitle, 'name': this.addUsers.addBookname, 'price': this.addUsers.addBookprice, 'number': this.addUsers.addBooknum }
                    const addBooks = JSON.stringify(addBook)
                    window.sessionStorage.setItem('addBooks', addBooks)  //将新添加的图书信息保存在浏览器中，防止丢失
                    this.tableData.unshift({ 'title': this.addUsers.addBooktitle, 'name': this.addUsers.addBookname, 'price': this.addUsers.addBookprice, 'number': this.addUsers.addBooknum })
                    this.$message.success('书籍添加成功!')
                    setTimeout(() => {
                        location.reload()
                    }, 2000);
                })
            }, handleTrue(index, res) {        //行修改图书信息对话框获取索引值的点击事件
                this.dialogVisible = true
                this.tableRelevant.index = index
                // console.log(index)
            }, handleFalse() {            //行修改图书信息对话框的关闭事件
                this.dialogVisible = false
            }, handleEditTrue() {     //行修改图书信息对话框的确认事件
                if (this.editBook.bookname == '' || this.editBook.remanname == '' || this.editBook.bookprice == '' || this.editBook.booknum == '') {
                    this.$message({
                        message: '图书信息未填写完整!'
                    })
                } else {
                    this.tableData[this.tableRelevant.index].title = this.editBook.bookname
                    this.tableData[this.tableRelevant.index].name = this.editBook.remanname
                    this.tableData[this.tableRelevant.index].price = this.editBook.bookprice
                    this.tableData[this.tableRelevant.index].number = this.editBook.booknum
                    this.$message.success('书籍信息修改成功!')
                }
                this.dialogVisible = false
                this.$refs.editBook.resetFields()   //关闭对话框后清空所填项
            }, async handleDelete(index, res) {        //行图书信息的删除事件
                const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).catch(err => err)
                // console.log(confirmResult)
                if (confirmResult !== 'confirm') {
                    return this.$message.info('已经取消删除操作')
                }
                this.$message.success('成功删除该用户')
                this.tableData.splice(index, 1)
            }
        }
    }
</script>

<style scoped lang='stylus'>

</style>